////////////////////////////////////////////////////////////////
// Variables
////////////////////////////////////////////////////////////////
$headlineWidth: calc(4vw + 30px);
$globalMargin: 10vw;
$sectionPadding: 100px 0;

////////////////////////////////////////////////////////////////
// Colors
////////////////////////////////////////////////////////////////
$lightColor: #eee;

#pitch,
#whatWeDo,
#onNumaraMotivasyon,
#team,
#story,
#specialties,
#references,
#gallery,
#allServices {
  background-color: $lightColor;
}

////////////////////////////////////////////////////////////////
// GENERAL Theme Properties
////////////////////////////////////////////////////////////////

.App {
    width: 100vw;
    overflow: hidden;
}

* {
    margin: 0;
    padding: 0;
}

.text,
.snippet,
.description {
    margin-top: 1em;
}

li {
    list-style: none;
}

button {
    width: 130px;
    height: 33px;
    margin-top: 1.5em;
    border: 1px solid #000;
    font-size: 1em;
}

.heading {
    display: flex;
    align-items: flex-end;
    // Global left margin
    margin-left: $globalMargin;

    &.small {
        margin-left: 0px;
    }

    .title {
        width: 200px;
    }
}

.section-heading {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    justify-content: flex-end;

    .title {
    }

    .line {
      width: calc(5vw + 20px)
    }
}

.lined {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
}

.media,
.picture,
.icon,
.image-container,
.background {
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      background: #ffffff url("../assets/sample-image.jpg") no-repeat center center;
}

.media,
.bg-video,
.background {
  filter: brightness(0.2);
}

////////////////////////////////////////////////////////////////
//// GENERAL Theme Properties - SHAPES
////////////////////////////////////////////////////////////////

.icon {
    border-radius: 100%;
}

.flex {
    display: flex;
}

.headline {
    height: 1px;
    background-color: #000;
}

.line {
    width: 30px;
    height: 1px;
    margin-right: 0.5em;
    margin-bottom: 0.3em;
    background-color: #000;
}

.icon-container {
    padding: 15px;
    pointer-events: all;
}

.menu-icon {
    width: 16px;
    height: 10px;
    transform: rotate(0deg);
    transition: 0.5s ease-in-out;
    cursor: pointer;
    display: flex;
    align-items: center;

    span {
        display: block;
        position: absolute;
        height: 2px;
        width: 100%;
        background: #000;
        border-radius: 1px;
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: 0.25s ease-in-out;
    }

    span:nth-child(1) {
        top: 0px;
    }

    span:nth-child(2) {
        top: 5px;
    }

    span:nth-child(3) {
        top: 10px;
    }
}

.linking .line {
    width: 15px;
}

////////////////////////////////////////////////////////////////
//// GENERAL Theme Properties - ANIMATIONS
////////////////////////////////////////////////////////////////

@keyframes headline-ani {
    0% {
        width: 0;
        left: $headlineWidth;
    }

    17% {
        width: $headlineWidth;
        left: 0px;
    }

    100% {
        width: 0;
    }
}

.headline {
    animation: headline-ani infinite 6s ease-in-out;
}

////////////////////////////////////////////////////////////////
/* HEADER */
////////////////////////////////////////////////////////////////

#header {
    position: fixed;
    width: 100vw;
    left: 0;
    top: 0;
    pointer-events: none;
    transition: all 0.2s ease;
    z-index: 1000;

    * {
        transition: all 0.2s ease-out;
    }

    &.hide {
        top: -100px;
        opacity: 0;
    }

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        z-index: 200;
        margin: 40px 25px;
    }

    &.mini .container {
        margin: 30px 10px;
    }

    .container .logo {
        pointer-events: all;
    }

    .menu-list {
        opacity: 0;
        display: none;
        justify-content: space-between;
        align-items: center;
        pointer-events: all;
        font-size: 0.75em;
        padding-right: 20px;

        &:hover .menu-item {
            p,
            .line {
              opacity: 0.3;
            }
        }

        .menu-item {
            width: 90px;
            display: flex;
            flex-direction: row-reverse;
            justify-content: flex-start;
            align-items: center;

            p {
                font-size: 0.9em;
                font-weight: 100;
                padding: 5px;
                color: $lightColor;
                min-width: 50px;
            }

            &:hover {
                p,
                .line {
                  opacity: 1;
                }
            }

            .line {
                width: 0px;
                margin-top: 1.1em;
                margin-right: 0;
                transition: width 0.2s ease-out;
                background-color: $lightColor;
            }

            &:hover .line,
            &.active .line {
                width: 10px;
            }
        }
    }
}


////////////////////////////////////////////////////////////////
/* MENU WINDOW */
////////////////////////////////////////////////////////////////

#menuWindow {
    display: none;
    position: fixed;
    background-color: #222;
    width: 100vw;
    height: 100vh;
    top: 0;
    color: #000;

    .line {
        display: block;
        position: relative;
        height: 10px;
    }

    .menu-list {
        display: flex;
        position: absolute;
        top: 210px;
        right: 50px;
        font-size: 2em;
        flex-direction: column;

        .menu-item {
            width: 200px;
            display: flex;
            justify-content: flex-end;
            flex-direction: row;
            align-items: flex-end;
            margin: 25px auto;

            p {
                padding: 0;
                font-weight: 400;
                width: 200px;
                text-align: right;
            }
        }
    }

    .follow-list {
        position: absolute;
        bottom: 30px;
        left: 20px;
        flex-wrap: wrap;

        .icon {
            width: 18px;
            height: 18px;
            margin: 5px;
            background-color: #aaa;
        }
    }

    .logo {
        filter: brightness(0) invert(1);
    }

    .statement {
        width: 150px;
        position: absolute;
        color: #ccc;
        font-size: 0.7em;
        font-weight: 100;
        bottom: 70px;
        left: 25px;
    }

    .line {
        width: 50px;
        margin-bottom: 0.2em;
        margin-left: 0.5em;
        border-bottom: 1px solid #000;
    }

    .menu-item.active .line {
        border-color: #ccc;
    }

    .menu-icon span {
        background-color: #ccc;
        height: 1px;

        &:nth-child(1) {
            width: 10px;
            top: 1px;
            left: 10px;
            transform: rotate(45deg);
        }

        &:nth-child(2) {
            width: 20px;
            left: 0px;
        }

        &:nth-child(3) {
            width: 10px;
            top: 9px;
            left: 10px;
            transform: rotate(-45deg);
        }
    }

    .menu-list .menu-item.active {
        color: #ccc;
    }
}

////////////////////////////////////////////////////////////////
/* FOOTER */
////////////////////////////////////////////////////////////////

#footer {
    background-color: #000;

    * {
        font-family: sans-serif;
        font-size: 0.9em;
        line-height: 1.75em;
        color: #eee;
    }

    .wrapper {
        max-width: 1000px;
        margin: auto;
        padding: 70px 40px 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .title {
        font-weight: 600;
    }

    .list {
        padding-right: 15px;
        min-width: 60px;
    }

    .container {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 500px;
    }

    .footlinks {
        margin: 2em 0;
        /* max-width: calc(100vw - 60px);
         */
        display: flex;
        justify-content: flex-start;
    }

    .copyright {
        margin-top: 30px;
        font-size: 0.7em;
        width: 100%;
    }

    .brand {
        min-width: 200px;

        img {
            filter: brightness(0) invert(1);
        }
    }

    .next {
        text-align: right;
        height: 50px;
    }
}

////////////////////////////////////////////////////////////////
/* WELCOME SECTIONs */
////////////////////////////////////////////////////////////////

.welcome {
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
    overflow: hidden;

    .bg-video {
      position: fixed;
      width: 100vw;
      height: 100vh;
      background: #000 url("../assets/sample-image.jpg") no-repeat right top;
      z-index: -100;

      .video {
        /* filter: brightness(0.2); */
        /* filter: grayscale(1); */
        /* filter: sepia(1); */
        /* filter: saturate(8); */
        /* filter: hue-rotate(90deg); */
        /* filter: invert(.8); */
        /* filter: opacity(.1); */
        /* filter: contrast(4); */
        /* filter: blur(5px); */
        /* filter: drop-shadow(16px 16px 10px rgba(0,0,0,0.9)); */
        /* filter: contrast(4) grayscale(1); */
      }
    }

    .content {
        .heading {
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            .title {
                max-width: 250px;
                height: 300px;
                display: flex;
                align-items: flex-end;
                color: white;
            }

            .headline {
                position: relative;
                width: $headlineWidth;
                background-color: $lightColor;
            }

            .text {
                padding-right: 10vw;
                max-width: 400px;
                height: 100px;
                color: white;
                opacity: 0.4;
            }

            .title,
            .text {
                position: relative;
