////////////////////////////////////////////////////////////////
/* PITCH */
////////////////////////////////////////////////////////////////

#pitch {
    padding: $sectionPadding;
    height: 130vh;
    display: flex;
    align-items: center;

    .container {
        margin: auto $globalMargin;
        width: 100%;
        .snippet {
            font-size: 1.5em;
            padding-right: 10vw;
        }
    }
}

////////////////////////////////////////////////////////////////
/* WHAT WE DO */
////////////////////////////////////////////////////////////////

#whatWeDo {
    padding: $sectionPadding;
    width: 100vw;
    min-height: 100vh;
    display: flex;
    align-items: center;

    .container, {
      width: 100%;
      margin: auto $globalMargin;

      .content {
        padding: 0 3vw;
        .hero {
          width: 100%;
          padding: 50px 0;

          .image-container {
            display: flex;
            justify-content: center;
            align-items: center;

            .video {
              width: 100%;
            }
          }

          .content-heading {
            .line {
                width: calc(3vw + 20px);
            }

            .title,
            .text {
                margin-left: calc(3vw + 20px + 1rem);
            }
          }

          .line {
            width: calc(3vw + 20px);
          }
        }

        .categories {
          display: flex;
          flex-wrap: wrap;
          padding: 25px 0;
          justify-content: space-between;

          .emlak,
          .insaat {
            width: 100%;
            padding: 50px 0;

            .image-container {
                width: 100%;
            }

            .content-heading {
              .line {
                  width: calc(1vw + 20px);
              }

              .title,
              .text {
                  margin-left: calc(1vw + 30px);
              }
            }
            button {
                margin-left: calc(1vw + 30px);
            }
          }
        }

        .hero,
        .categories {
          .image-container {
            height: 300px;
          }

          .content-heading {
            padding: 1em 0;
          }
        }
      }
    }
}

////////////////////////////////////////////////////////////////
/* ON NUMARA MOTIVASYON */
////////////////////////////////////////////////////////////////

#onNumaraMotivasyon {
    padding: $sectionPadding;
    width: 100vw;
    height: 100vh;
    padding-left: calc(5vw + 30px + 1em);
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .container {
        max-width: 250px;
    }
}

////////////////////////////////////////////////////////////////
/* TEAM */
////////////////////////////////////////////////////////////////

#team {
    padding: $sectionPadding;

    .container {
      margin: auto $globalMargin;

      .list {
          margin: 30px 0;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-around;

          .item {
              margin: 40px 0;
              display: flex;
              flex-direction: column;
              align-items: center;
              padding: 20px;

              * {
                text-align: center;
              }

              .picture {
                  width: 200px;
                  height: 250px;
              }

              .name {
                  margin: 1em 0;
              }

              .snippet {
                  width: 280px;
              }
          }
      }
    }
}

////////////////////////////////////////////////////////////////
/* BIOGRAPHY */
////////////////////////////////////////////////////////////////

#biography {
    width: 70vw;
    max-width: 800px;
    margin: 150px auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    .picture {
        width: 70vw;
        max-width: 350px;
        height: 350px;
        margin: 0 auto;
    }

    .content {
        width: 70vw;
        max-width: 350px;
        margin: 0 auto;
    }

    .heading {
        margin: 0;
        padding-top: 40px;
    }

    a {
        font-size: 1em;
    }

    .title {
        margin-top: 50px;
    }

    .intro,
    .bio {
        margin-top: 20px;
    }

    .career-history.list {
        margin: 60px 10px;
        font-size: 0.8em;
        width: 300px;

        .item {
            display: flex;
            flex-wrap: nowrap;
            margin-top: 30px;

            .dates {
                min-width: 80px;
                margin-right: 10px;
                text-align: right;
            }

            .company,
            .position {
                max-width: 150px;
            }

            .note {
                display: block;
                font-weight: 100;
                margin: 5px auto;
                color: #999;
            }
        }
    }

    .educations {
        .title {
            padding-top: 30px;
        }

        .info {
            line-height: 2em;
        }

        .item {
            margin: 40px auto;
            font-size: 0.9em;

            .activity {
                list-style: disc;
                list-style-position: inherit;
                margin: 20px 25px;
            }
        }
    }
}

////////////////////////////////////////////////////////////////
/* STORY */
////////////////////////////////////////////////////////////////

#story {
    padding: $sectionPadding;
    min-height: 100vh;

    .container {
        margin: 0 $globalMargin;

        .content {
          margin: 30px 0;

          .reason,
          .focus,
          .challenge {
            margin: 50px 0;
          }
        }
    }
}

////////////////////////////////////////////////////////////////
/* SPECIALTIES */
////////////////////////////////////////////////////////////////

#specialties {
    /* padding-top: 150px; */
    /* padding-bottom: 150px; */
    padding: $sectionPadding;

    .container {
      margin: 0 $globalMargin;

      .content {
          margin-left: calc(5vw + 20px);
          .list {
              display: flex;
              flex-wrap: wrap;
              margin: 30px 0;
              .item {
                  display: flex;
                  align-items: center;
                  margin: 10px 0;

                  .bullet {
                    font-size: 30px;
                    font-weight: 100;
                    padding: 0px 5px 3px 5px;
                  }

                  .title {
                      width: 300px;
                      font-size: 21px;
                  }

                  .bullet,
                  .title {
                    color: #444;
                  }
              }
          }
      }
    }
}

////////////////////////////////////////////////////////////////
/* REFERENCES */
////////////////////////////////////////////////////////////////

#references {
    padding: $sectionPadding;
    position: relative;

    .container {
      .section-heading {
        margin: 0 $globalMargin;
      }

      .content {
        .list {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 25px 0;

            .item {
                width: 41%;
                padding: 2vw 0;
                display: flex;
                justify-content: center;

                .image-container {
                  width: 80px;
                  height: 60px;
                  margin: 20px;
                }
            }
        }

        .more {
            display: block;
            text-align: center;
        }
      }
    }
}

////////////////////////////////////////////////////////////////
/* GALLERY */
////////////////////////////////////////////////////////////////

#gallery {
  padding: $sectionPadding;
  min-height: 100vh;

  .container {
    .section-heading {
      margin: 0 $globalMargin;
    }

    .content {
      width: 100vw;
    }
  }

  .demo4 {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 700px;
    position: relative;
    transform: scale(0.8);

    .buttons {
      position: absolute;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      top: -110px;
    }

    .next,
    .prev {
      margin: 0 10px;
      cursor: pointer;
    }

    .next {
    }
  }

  .demo4-inner {
    overflow: hidden;
    position: relative;
    margin: auto;
  }

  .demo4-photo {
    position: absolute;
    background-color: lightgray;
  }
}

////////////////////////////////////////////////////////////////
/* ALL SERVICES */
////////////////////////////////////////////////////////////////

#allServices {
    padding-top: 100px;
    padding-bottom: 100px;

    .top .filter {
        padding-left: calc(55px + 1em);
        color: #777;
        display: flex;
        padding-left: calc(5vw + 30px + 1em);

        .item {
            margin-top: 10px;
            padding: 10px 20px 10px 0;
        }
    }

    .container {
        width: 80vw;
        margin: auto;
    }

    .service-list {
        margin-top: 5vw;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;

        .item {
            margin-top: 65px;
            padding-bottom: 5vw;
            width: 400px;
            height: 500px;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;

            .content {
                margin-right: 20px;
                padding: 0 10px;
            }

            .heading {
                position: relative;
                margin: 20px 0;

                .title {
                    width: 100%;
                }
            }

            .description {
                position: relative;
                padding-left: calc(20px + 0.8em);
            }

            button {
                position: relative;
                margin-left: calc(20px + 0.8em);
            }
        }
    }

    .media {
        height: 200px;
    }
}

////////////////////////////////////////////////////////////////
/* SERVICE */
////////////////////////////////////////////////////////////////

#service {
    background-color: $lightColor;

    .heading {
        margin-top: 50px;
    }

    .media {
        position: fixed;
        top: 0;
        height: 310px;
        width: 100vw;
        z-index: -100;
    }

    .info {
        position: relative;
        max-width: 100vw;
        margin: auto;
        margin-top: 300px;
        padding-right: 30px;
        padding-top: 30px;
    }

    .container {
        max-width: 800px;
        margin: 20px 0 0;
        padding: auto;
        padding-left: calc(5vw + 20px + 1em);

        .title {
            margin-top: 60px;
        }

        .intro {
            margin: 40px 0;
        }
    }

    .item {
        list-style: disc;
        list-style-position: inherit;
        margin: 25px;
    }

    .filter {
        font-size: 0.8em;
        width: 110px;
        justify-content: space-between;
        margin-top: 10px;
    }

    button {
        margin: 100px 0;
    }
}

////////////////////////////////////////////////////////////////
/* CONTACT */
////////////////////////////////////////////////////////////////

#contact {
    height: 80vh;
    margin-left: 10vw;
    padding-top: 40vh;

    .heading {
      .title {
        color: $lightColor;
      }

      .line {
        background-color: $lightColor;
      }
    }

    .background {
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100%;
      z-index: -100;
    }

    .list {
        margin-left: calc(5vw + 30px + 1em);
        margin-top: 70px;

        h3 {
            color: $lightColor;
            margin-top: 20px;
        }

        p,
        a {
            display: block;
            font-size: 1em;
            color: #777;
            margin-top: 10px;
        }
    }
}

////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////
/* MEDIA QUERIES */
////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////

@media only screen and (min-width: 768px) {
    #header {
        .container {
            margin: 60px 50px;
        }

        .menu-list {
            opacity: 1;
            display: flex;
        }

        &.mini .menu-list {
            opacity: 0;
        }

        &.mini .container {
            margin: 30px;
        }

        .navigation {
            width: 500px;
            justify-content: flex-end;
            align-items: center;
        }
    }

    #whatWeDo {
      .container {
        .categories {
          justify-content: space-between;

          .emlak,
          .insaat {
            max-width: 46.6%;

            .image-container {
                width: 100%;
            }
          }
        }

        .hero,
        .categories {
          .image-container {
            height: 300px;
          }

          .content {
            .title {
              display: block;
            }
          }
        }
      }
    }

    .welcome {
        .content {
            .heading {
                .title {
                    min-width: 500px;
                }
            }
        }
    }

    #onNumaraMotivasyon {
        justify-content: flex-end;
    }

    #onNumaraMotivasyon .container {
        padding-top: 0;
        max-width: 400px;
        margin: 300px;
    }

    #story {
      .container {
        .content {
          margin: 30px calc(5vw + 20px + 10px);
        }
      }
    }

    #allServices .service-list {
        margin-top: 3vw;

        .description {
            height: 100px;
        }

        .heading {
            height: 40px;
        }

        .item {
            margin: 25px;
            padding-bottom: 0vw;
        }
    }

    #wrapper {
        padding: 70px 40px 30px;
    }

    #footer {
        .container {
            width: calc(40vw + 100px);
            flex-direction: row;
            justify-content: space-between;
        }

        .footlinks {
            margin-top: 0;
        }

        .next {
            right: 50px;
            top: 50px;
            text-align: right;
        }
    }

    #gallery {
        .collage {
            display: flex;
            justify-content: center;
            width: 700px;
            margin: auto;
            padding: 10px;
        }

        .area-1,
        .area-2 {
            display: flex;
            margin-top: 70px;
            justify-content: space-evenly;
            flex-wrap: wrap;
        }

        .image-container {
            height: 250px;
            margin: 2px;
        }

        .area-1 .image-container:nth-child(2n) {
            height: 400px;
        }

        .area-2 .image-container:nth-child(n) {
            height: 300px;
        }
    }

    #menuWindow {
        .menu-list {
            top: 30vh;
            right: 10vw;
            font-size: 2em;

            .menu-item {
                margin: 30px auto;

                p {
                    font-weight: 400;
                }
            }
        }

        .follow-list {
            bottom: 60px;
            left: 50px;
        }

        .statement {
            bottom: 100px;
            left: 55px;
        }
    }

    #references {
      .container {
        .content {
          padding: 0 10vw;
          .list {
            .item {
              width: 20vw;
            }
          }
        }
      }
    }

    #service .info {
        padding: 30px 10vw;
    }

    #team .container .list .item {
        text-align: left;
    }
}

@media only screen and (min-width: 630px) {
    #allServices .top {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        width: calc(90vw - 60px - 1em);
        margin: auto;

        .heading {
            margin: 0;
        }

        .filter {
            padding: 0;

            .item {
                padding: 0 10px;
                min-width: 50px;
            }
        }
    }

    #footer .next {
        text-align: left;
    }
}

@media only screen and (min-width: 900px) {
    #biography {
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: row;

        .content {
            margin-left: 50px;
        }
    }
}

