.headline {
  height: 1px;
  background-color: #000; }

.line {
  width: 30px;
  height: 1px;
  margin-right: 0.5em;
  margin-bottom: 0.3em;
  background-color: #000; }

.icon-container {
  padding: 15px;
  pointer-events: all; }

.menu-icon {
  width: 16px;
  height: 10px;
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;
  display: flex;
  align-items: center; }
  .menu-icon span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background: #000;
    border-radius: 1px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: 0.25s ease-in-out; }
  .menu-icon span:nth-child(1) {
    top: 0px; }
  .menu-icon span:nth-child(2) {
    top: 5px; }
  .menu-icon span:nth-child(3) {
    top: 10px; }

.linking .line {
  width: 15px; }

@keyframes headline-ani {
  0% {
    width: 0;
    left: calc(4vw + 30px); }
  17% {
    width: calc(4vw + 30px);
    left: 0px; }
  100% {
    width: 0; } }

.headline {
  animation: headline-ani infinite 6s ease-in-out; }

/* HEADER */
